import React from "react";

function AboutWhoWeAre() {
  return (
    <div className="container-fluid block">
      <div className="container">
        <div className="titleHolder">
          <h2>Who We Are</h2>
        </div>
        <div className="contentHolder">
          <p>
            GEMS School was established in 1984 in Sanepa, Lalitpur with 78
            students and 25 staff members. Initially, classes from Nursery to
            grade III were offered. Today, it has become a full-fledged higher
            secondary school running classes from grade one to twelve. Upon
            completion of the construction of its own school complex at
            Dhapakhel, which is 1.5 kilometers away from Sat Dobato point on the
            Ring road, it shifted all classes from four to ten from Kiran
            Bhawan, Sanepa to the new complex in August 2000. From 2077 it
            started running classes from one to ten in the new complex.
          </p>
          <p>
            The school has excellent infrastructure supported by
            state-of-the-art facilities. It has modern academic buildings with
            spacious classrooms, laboratories, conference hall, hostel
            buildings, administrative offices, a cafeteria and spacious playing
            grounds for games and sports such as football, basketball, etc.
            There are separate buildings and rooms for extra-curricular
            activities such as gymnastics, judo, keyboard, percussion, guitar,
            violin, sitar, etc.
          </p>
        </div>
      </div>
    </div>
  );
}

export default AboutWhoWeAre;
